#schedule {
  .btn-outline-danger {
    display: inline-block;
    width: 100%;
    margin-top: 12px;
    align-items: center;
    white-space: nowrap;
  }
}
.error {
  margin-top: 0.5rem;
  color: $color-dark;
}
.terms {
  border: 1px solid $color-red;
  padding: 0.4rem;
  max-height: 150px;
  overflow-y: scroll;
}
.modal-content {
  margin: 1.5rem;
}
